<template>
    <div>
        <app-menu :items_menu="items_menu" languaje="es" :api="true"></app-menu>


        <app-api-feriados></app-api-feriados>


    </div>

</template>
<script>
    import AppMenu from './page/menu.vue'
    import AppAPIFeriados from './page/api-feriados.vue'

    export default {
        components: {
            'app-menu': AppMenu,
            'app-api-feriados': AppAPIFeriados,
        },
        data ()
        {
            return {
            items_menu: [{
                'id': '#feriados',
                'descripcion': 'API de feriados'
            },
            {
                'id': '#provincias',
                'descripcion': 'API de provincias'
            },
            {
                'id': '#partidos',
                'descripcion': 'API de partidos'
            }]

            }
        }
    }
</script>


