import Vue from 'vue'
import App from './App.vue'
import AppEs from './App-es.vue'
import AppEn from './App-en.vue'
import AppApi from './App-api.vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/es',
      component: AppEs
    },
    {
      path: '/en',
      component: AppEn
    },
    {
      path: '/api',
      component: AppApi
    },
    {
      path: '*',
      redirect: '/es'
    }
  ]
});

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



